/* Google fonts imported  */
@import url('https://fonts.googleapis.com/css2?family=Bad+Script&family=Cinzel&family=Julius+Sans+One&family=Rock+Salt&family=Rubik+Glitch&display=swap');

/* Navbar css  */

#root {
  text-align: center;
  text-decoration: none;
}

h2 {
  margin: 4rem auto;
  font-family: "Cinzel", serif;
  font-weight: bolder;
}

#navbar h1 {
  font-size: 4rem;
}

#navbar h1,
h5 {
  margin-top: 2rem;
  font-family: "Cinzel", serif;
}

#navbar ul li {
  text-decoration: none;
  list-style-type: none;
  font-family: "Bad Script", "cursive";
}

#navbar li:hover {
  text-decoration: line-through;
}

#navbar {
  margin: 0 2rem;
}

#navbar ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 20px;
  text-decoration: none;
  border: 1px solid grey;
  margin-top: 3rem;
  padding: 1.3rem;
}

/* Navbar css End */

/* Navbar Hamburger menu  */


@media only screen and (max-width: 520px) {

  #navbar ul {
    display: flex;
    flex-direction: column;
    height: 60vh;
  }
}

/* Navbar Hamburger menu End  */

/* About me page  */

#about {
  margin: 4rem 15%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#aboutPic {
  margin: 2rem;
}

#aboutPic img {
  height: 15rem;
}

#resumeDiv {
  text-align: center;
  padding: 4rem 0 1.5rem 0;
}

#resumeDiv a {
  text-decoration: underline;
  font-weight: 500;
}

#resumeDiv a:hover {
  content: "Download";
  font: 1em 'Rock Salt';
  text-decoration: line-through;
}

/* About me page End */

/* Skills */
#skills {
  display: flex;
  flex-direction: column;
  margin: 3rem 2rem;
}

#icons {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  column-gap: 8rem;
  align-items: center;
  font-size: 10rem;
  width: 100%;
  filter: grayscale(100%);
}

#icons:hover {
  filter: none;
}

@media only screen and (max-width: 640px) {
  #icons {
    font-size: 5rem;
    column-gap: 3rem;
  }
}

/* skills end  */

/* Contact  */

.contact-container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.contact-form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
}

#contact {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-transform: uppercase;
  font-size: clamp(20px, 10vw, .7rem);
  font-weight: 300;
  opacity: 0.6;
  letter-spacing: 6px;
  padding-bottom: 80px;
  width: 50%;
}

#contact a {
  padding: 1rem;
  margin: 1rem;
  text-decoration: none;
}

#contact a:hover {
  text-decoration: line-through;
}

.react-icons {
  font-size: 2.5rem;
  padding-right: 10px;
  vertical-align: middle;
}

.contact-between-line::after {
  content: "";
  position: absolute;
  width: 2px;
  height: 100%;
  background-color: grey;
  left: 50%;
  top: 0;
}

@media screen and (max-width:1100px) {
  #contact {
    width: 100%;
  }

  .contact-container {
    display: block;
    justify-content: center;
    align-items: center;
    position: relative;
  }

  .contact-between-line::after {
    transform:rotate(90deg);
    position: absolute;
    height: 40%;
    top: 38%;
  }

  .contact-form-container {
    width: 100%;
  }

}

/* Contact end  */

/*  Work styling  */

.cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin: 4rem 5vw;
  padding: 0;
  list-style-type: none;
}

.card {
  position: relative;
  display: block;
  border-radius: calc(var(--curve) * 1px);
  overflow: hidden;
  text-decoration: none;
  border-radius: 15px;
  background: #000;
}

.card__image {
  width: 100%;
  height: 70%;
  border-radius: 15px;
}

.card__overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  border-radius: calc(var(--curve) * 1px);
  background-color: var(--surface-color);
  transform: translateY(100%);
  transition: 0.2s ease-in-out;
}

.card:hover .card__overlay {
  transform: translateY(0);
  background-color: rgba(0, 0, 0, 0.9);
}

.card__header {
  position: relative;
  display: flex;
  align-items: center;
  gap: 2em;
  padding: 2em;
  border-radius: calc(var(--curve) * 1px) 0 0 0;
  background-color: var(--surface-color);
  transform: translateY(-100%);
  transition: 0.2s ease-in-out;
}

.card__arc {
  width: 80px;
  height: 80px;
  position: absolute;
  bottom: 100%;
  right: 0;
  z-index: 1;
}

.card__arc path {
  fill: var(--surface-color);
}

.card:hover .card__header {
  transform: translateY(0);
}

.card__title {
  font-size: 1.5em;
  margin: 0 0 0.3em;
  padding: 10px;
  background: #000;
  color: #fff;
  opacity: 0.9;
  font-family: "Julius Sans One", sans-serif;
  font-size: 2rem;
}

.card__tagline {
  display: block;
  margin: 1em 0;
  font-family: "MockFlowFont";
  font-size: 0.8em;
  color: #d7bdca;
}

.card__status {
  font-size: 1.1em;
  color: #fff;
}

.card__description {
  padding: 0 2em 2em;
  margin: 0;
  color: #fff;
  font-family: "MockFlowFont";
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
}

/* Work styling end  */

/* button styling  */

.btn {
  border: none;
  text-align: center;
  cursor: pointer;
  text-transform: uppercase;
  outline: none;
  overflow: hidden;
  position: relative;
  color: #fff;
  font-weight: 700;
  font-size: 15px;
  background-color: #222;
  padding: 17px 60px;
  margin: 10px 20px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.btn span {
  position: relative;
  z-index: 1;
}

.btn:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 490%;
  width: 140%;
  background: #78c7d2;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  -webkit-transform: translateX(-98%) translateY(-25%) rotate(45deg);
  transform: translateX(-98%) translateY(-25%) rotate(45deg);
}

.btn:hover:after {
  -webkit-transform: translateX(-9%) translateY(-25%) rotate(45deg);
  transform: translateX(-9%) translateY(-25%) rotate(45deg);
}

/* button styling end  */

/* Footer  */

#footer {
  text-transform: uppercase;
  font-weight: 300;
  font-size: 15px;
  opacity: 0.6;
  letter-spacing: 6px;
  text-align: center;
  padding: 80px 16px;
  border-bottom: grey;
}

/* Footer End */

hr {
  width: 80%;
  height: 3px;
  background-color: grey;
  margin-bottom: 40px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 60px;
}

/* Scroll to top button css  */
.backtotop-btn {
  position: fixed;
  bottom: 0px;
  right: 10px;
  border: none;
  font-size: 4rem;
  background: none;
  color: inherit;
  z-index: 10;
}


/* Scroll to top button css End */


/* Contact form css  */

@import url(https://fonts.googleapis.com/css?family=Lato:100,300,400);

#contact-form {
  position: relative;
  width: 500px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

input {
  font-family: 'Lato', sans-serif;
  font-size: 0.875em;
  width: 470px;
  height: 50px;
  padding: 0px 15px 0px 15px;
  
  background: transparent;
  outline: none;
  color: #726659;
  
  border: solid 1px #b3aca7;
  border-bottom: none;
  
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
}

input:hover {
  background: #b3aca7;
  color: #e2dedb;
}

textarea {
  width: 470px;
  max-width: 470px;
  height: 110px;
  max-height: 110px;
  padding: 15px;
  
  background: transparent;
  outline: none;
  
  color: #726659;
  font-family: 'Lato', sans-serif;
  font-size: 0.875em;
  
  border: solid 1px #b3aca7;
  
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
}

textarea:hover {
  background: #b3aca7;
  color: #e2dedb;
}

#contact-submit {
  
  padding: 0;
  margin: -5px 0px 0px 0px;
  
  font-family: 'Lato', sans-serif;
  font-size: 0.875em;
  color: #b3aca7;
  
  outline:none;
  cursor: pointer;
  
  border: solid 1px #b3aca7;
  border-top: none;
}

#contact-submit:hover {
  color: #e2dedb;
}

@media screen and (max-width: 550px) {
  input {
    width: 300px;
  }

  textarea {
    width: 300px;
  }
}

/* Contact form css End */
